}
.loading{
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slider-movies{
    height: 53vh;
    width: 100%;
}

.slider-movies__movie{
    height: 55vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.slider-movies__movie-info{
    display: flex;
    align-items: flex-end;
    height: 100%;
    padding-bottom: 50px;
}
            
.div-info{
    background-color: #8d8c8ccf;
    margin: 0 auto;
    text-align: center;
    width: 60%;
    padding-bottom: 13px;
    color: black;
}

.boton{
    padding-bottom: 5px;
    color:rgb(212, 202, 202);
    border-radius: 70px;
}

.titulo{
    color: rgb(39, 39, 39);
}

.parrafo{
    padding-bottom: 10px;
    color: rgb(36, 36, 36);
}




@media ( max-width: 1000px ){
    .slider-movies__movie-info div{
        width: 90%
    }
}
  
@media ( max-width: 500px ){
    .slider-movies__movie-info div{
        font-size: 10px;
        width: 95%;
        padding: 5px;
    }
    .boton{
        font-size: 10px;
        height: 20px;
        padding: 1px 5px;
    }
}
  
.movie-list{
    width: 70%;
    margin: 0 auto !important; 
    margin-top: 60px !important; 
    border-color: rgb(65, 120, 122) !important;
}

.ant-list-header{
    background-color: rgb(45 41 51);
}

.ant-list-header h2{
    text-align: center;
    margin: 0;
    color: white;
}

.ant-list-items{
    height: 60vh;
    overflow: scroll;
    background-color: rgb(176, 188, 189);
}

.ant-list-item-meta{
    align-items: center;
}

.ant.list.item-meta-title{
    margin: 0
}

.boton-info{
    background-color: rgb(101, 97, 109);
}

.ant-row{
    padding-bottom: 90px;
}
@media ( max-width: 767px ){
  .ant-col{
    width: 100%;
    padding: 0;
  }
}

@media ( max-width: 767px ){
  .ant-row{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .ant-col-12 {
    max-width: 100% !important;
  }
}




.modal-video{
    width: 100% !important;
    height: 100% !important;
    padding: 0 !important;
}

.ant-modal-content{
    height: 100%;
    background-color: black !important;
}

.ant-modal-close{
    color: white;
}

.ant-modal-close span{
    font-size: 1em;
    margin-right: 20%;
    margin-top: 20%;
}

.ant-modal-body{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

  .ant-modal-body div{
    width: 90% !important;
    height: 90% !important;
} 
.movie{
    height: 103vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.movie__dark{
    position: absolute;
    top: 0;
    left: 0;
    height: calc( 100vh + 130px );
    width: 100%;
    background-color: black;
    opacity: .6;
}

.row-movie{
    height: 100vh !important;
    display: flex !important;
    align-items: center !important;
    width: 100%;
}

.movie__poster{
    height: 60vh;
}

.movie__poster div{
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.movie__info{
    color: white;
    height: 75%;
}

.movie__info-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.titulo-info-header{
    color: white;
    margin: 0;
    font-size: 2.5em;
    width: -webkit-fill-available;
}

.span-info-header{
    font-size: .5em;
    margin-left: 2%;
    opacity: .6;
}

.boton-trailer{
    height: 100%; 
    background-color: transparent;
    color: #ffffff;
    border: 0;
    font-size: 1.3em;
    border-radius: 40px;
}


.letras-info{
    color: rgb(170, 170, 170);;
    font-weight: bold;
    font-size: 1em ;
}



@media ( max-width: 1250px ){
  .row-movie{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .movie,
  .movie__poster{
    align-self: center;
    margin-left: 0;
    margin-right: 0;
  }

  .ant-col-9{
    max-width: 80%;
  }

  .movie__info-content{
    width: 75%;
  }

  .movie-info{
    width: 100%;
  }

  .buton-trailer{
    font-size: 25px;
    padding: 0;
  }

  .info{
    flex-direction: column;
    font-size: 25px;
  }

  .movie__info-content{
    margin-left: 10px;
    font-size: 25px;
  }


}

@media ( max-width: 750px ){
    .movie,
    .movie__poster{
        margin-right: 50px;
    }

    .movie__poster  div{
        width: 250px;
    }
}

@media ( max-width: 480px ){
    .movie,
    .movie__poster div{
        margin-left: -40px;
    }

    .button{
        font-size: 10px;
        padding: 0;
    }

    .parrafo-info{
        font-size: 10px;
    }
}

@media ( max-width: 420px ){
    .movie,
    .movie__poster div{
        margin-left: -40px;
    }
}

@media ( max-width: 380px ){
  .movie-info{
    width: 100%;
  }
}
